.App {
    padding: 100px 0;
}

form.d-flex {
    display: block !important; /* Due to bootstrap...*/
    width: 100%;
}

form.d-flex .input {
    margin: 1em 0;
    width: 100%;
    display: block;
}

form.d-flex button {
    display: block;
}

@media (min-width: 960px) {
    form.d-flex {
        width: 69%;
        display: flex !important; /* Due to bootstrap...*/
    }

    form.d-flex .input {
        margin: 0 1em;
    }

    form.d-flex button {
        padding: 0 4em;
        margin: 0 1rem;
    }
}

